<template>
  <div class="personal-content-body">
    <div class="applytempbox">
      <div class="table-content" :class="{'table-content-hide':showright}">
        <div class="clear table-content-title">
          <div class="left">审批流程配置</div>
        </div>
        <div class="table-group">
          <div class="table-group-content">
            <div class="clear table-group-title">
              <div class="left breadcrumb">
                <el-breadcrumb separator-class="el-icon-arrow-right">
                  <el-breadcrumb-item :to="{ path: '/home/applytempList' }">审批模版列表</el-breadcrumb-item>
                  <el-breadcrumb-item>审批流程配置</el-breadcrumb-item>
                </el-breadcrumb>
              </div>
              <div class="line"></div>
            </div>
            <div class="applytemppro_box">
              <div class="atp_group">
                <div class="atp_group_title">默认审批流程</div>
                <div class="atp_group_content">
                  <div class="atp_peoplelist">
                    <div class="atp_peopleitem">
                      <div class="atp_peopleitem_left">默认审批人</div>
                      <div class="atp_peopleitem_right">
                        <draggable v-model="applierList" :options="{group:{ name:'control' }}">
                          <transition-group tag="div" class="flex flex_wrap" id="apply">
                            <div class="flex" v-for="(item,index) in applierList" :key="index">
                              <div class="atp_peopleitem_addeditem" @click="editApplyPeople(index,item)">
                                <span class="iconfont icon-guanbi" @click.stop="deleteApplyPeople(index)"></span>
                                <div class="atp_peopleitem_addeditem_top">
                                  <div class="atp_peopleitem_peoplebox" v-if="item.class==1">
                                    <img src="../../../static/images/appltTemp_icon.png" />
                                    <span>主管理员</span>
                                  </div>
                                  <div class="atp_peopleitem_peoplebox" v-for="user in item.user" v-if="item.class==2||item.class==3">
                                    <img v-if="item.class==2" :src="user.url" />
                                    <img v-else src="../../../static/images/appltTemp_icon.png" />
                                    <span>{{user.name}}</span>
                                  </div>
                                </div>
                                <div class="atp_peopleitem_addeditem_bottom" v-if="item.name!=''">
                                  {{item.name}}
                                </div>
                              </div>
                              <div class="atp_peopleitem_icon marginlr_10"><span class="iconfont icon-xiajiantou"></span></div>
                              <div class="atp_peopleitem_icon" @click="addApplyPeople" v-if="index==applierList.length-1">
                                <div class="atp_peopleitem_addicon"></div>
                              </div>
                            </div>
                          </transition-group>
                        </draggable>
                        <div class="atp_peopleitem_icon" @click="addApplyPeople" v-if="applierList.length==0">
                          <div class="atp_peopleitem_addicon"></div>
                        </div>
                      </div>
                    </div>
                    <div class="atp_peopleitem">
                      <div class="atp_peopleitem_left">默认抄送人</div>
                      <div class="atp_peopleitem_right">
                        <div class="atp_peopleitem_addeditem marginr_10" v-if="copierList.length!=0" v-for="(item,index) in copierList">
                          <span class="iconfont icon-guanbi" @click="deleteUser('copier',item.id,index)"></span>
                          <div class="atp_peopleitem_addeditem_top">
                            <div class="atp_peopleitem_peoplebox">
                              <img :src="item.url" @error="getImgurl" />
                              <span>{{item.name}}</span>
                            </div>
                          </div>
                        </div>
                        <!-- <div class="atp_peopleitem_text">未设置（不抄送给任何人)</div> -->
                        <div class="atp_peopleitem_icon" @click="addCopyPeople">
                          <div class="atp_peopleitem_addicon"></div>
                        </div>
                      </div>
                    </div>
                    <div class="atp_peopleitem" v-if="type==2||type==3">
                      <div class="atp_peopleitem_left">默认经办人</div>
                      <div class="atp_peopleitem_right">
                        <div class="atp_peopleitem_addeditem marginr_10" v-if="operatorList.length!=0" v-for="(item,index) in operatorList">
                          <span class="iconfont icon-guanbi" @click="deleteUser('operator',item.id,index)"></span>
                          <div class="atp_peopleitem_addeditem_top">
                            <div class="atp_peopleitem_peoplebox">
                              <img :src="item.url" @error="getImgurl" />
                              <span>{{item.name}}</span>
                            </div>
                          </div>
                        </div>
                        <!-- <div class="atp_peopleitem_text">未设置（不抄送给任何人)</div> -->
                        <div class="atp_peopleitem_icon" @click="addOperatorPeople">
                          <div class="atp_peopleitem_addicon"></div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="full_line"></div>
              <div class="atp_group">
                <div class="atp_group_title">
                  <div class="atp_group_title_left">分条件审批
                    <el-tooltip class="atp_group_title_tip" effect="dark" content="Top Left 提示文字" placement="top">
                      <i class="el-icon-question"></i>
                    </el-tooltip>
                  </div>
                  <div class="atp_group_title_right">
                    <span class="btn-blue-border" @click="addCondition">添加</span>
                    <span class="">审批单首先匹配优先级高的条件，拖拽可调整以下条件的优先级</span>
                  </div>
                </div>
              </div>
              <draggable v-model="conditionList" @update="removeHandle" :options="{group:'people',animation: 120}">
                <transition-group tag="div" classs="atp_conditionlist">
                  <div class="atp_conditionitem" v-for="(item,index) in conditionList" :key="index">
                    <div class="atp_conditionitem_left">
                      <div class="atp_conditionitem_leftspan">
                        <div class="title">优先级</div>
                        <div class="num">{{index+1}}</div>
                      </div>
                    </div>
                    <div class="atp_conditionitem_right atp_conditionbox">
                      <div class="atp_conditionbox_top">
                        <div class="atp_conditionbox_top_left">
                          <div class="text">{{item.category}}</div>
                        </div>
                        <div class="atp_conditionbox_top_right">
                          <span class="hoverlink" @click="copyAplierFun(index)">复制审批人</span>
                          <span class="hoverlink" @click="pasteAplierFun(index)">粘贴审批人</span>
                          <span @click="editCondition(index)">编辑</span>
                          <span @click="deleteConditionFun(item.id,index)">删除</span>
                        </div>
                      </div>
                      <div class="atp_peopleitem atp_conditionbox_bottom">
                        <div class="atp_peopleitem_left">审批人</div>
                        <div class="atp_peopleitem_right">
                          <div class="flex" v-for="(sp,index1) in item.sp" v-if="item.sp.length!=0">
                            <div class="atp_peopleitem_addeditem">
                              <div class="atp_peopleitem_addeditem_top">
                                <div class="atp_peopleitem_peoplebox" v-if="sp.class==1">
                                  <img src="../../../static/images/appltTemp_icon.png" />
                                  <span>主管理员</span>
                                </div>
                                <div class="atp_peopleitem_peoplebox" v-for="user in sp.user" v-if="sp.class==2||sp.class==3">
                                  <img v-if="sp.class==2" :src="user.url" />
                                  <img v-else src="../../../static/images/appltTemp_icon.png" />
                                  <span>{{user.name}}</span>
                                </div>
                              </div>
                              <div class="atp_peopleitem_addeditem_bottom" v-if="sp.name!=''">
                                {{sp.name}}
                              </div>
                            </div>
                            <div class="atp_peopleitem_icon" v-if="index1!=item.sp.length-1"><span class="iconfont icon-xiajiantou"></span></div>
                          </div>
                          <div v-if="item.sp.length==0" class="atp_peopleitem_text">当前条件下未设置审批人，匹配该条件时，可在审批单发起时自由添加审批人</div>
                        </div>
                      </div>
                      <div class="atp_peopleitem atp_conditionbox_bottom">
                        <div class="atp_peopleitem_left">经办人</div>
                        <div class="atp_peopleitem_right">
                          <div class="flex" v-if="item.handle.user!=undefined&&item.handle.user.length!=0">
                            <div class="atp_peopleitem_addeditem marginr_10" v-for="(user,index1) in item.handle.user">
                              <div class="atp_peopleitem_addeditem_top">
                                <div class="atp_peopleitem_peoplebox">
                                  <img :src="user.url" />
                                  <span>{{user.name}}</span>
                                </div>
                              </div>
                            </div>
                          </div>
                          <div v-else class="atp_peopleitem_text">未设置（不抄送给任何人)</div>
                        </div>
                      </div>
                    </div>
                  </div>
                </transition-group>
              </draggable>
              <div class="full_line"></div>
              <div class="atp_group">
                <div class="atp_group_title">
                  更多设置
                </div>
                <div class="atp_group_content">
                  <div class="atp_peoplelist">
                    <div class="atp_peopleitem marginb_30">
                      <div class="atp_peopleitem_left">审批人去重</div>
                      <div class="atp_peopleitem_right">
                        <div class="atp_peopleitem_inputbox">
                          <el-select v-model="filter" placeholder="请选择" size="small">
                            <el-option v-for="data in clearRepeatList" :key="data.id" :label="data.name" :value="data.id">
                            </el-option>
                          </el-select>
                        </div>
                      </div>
                    </div>
                    <div class="atp_peopleitem marginb_30">
                      <div class="atp_peopleitem_left">通知抄送人</div>
                      <div class="atp_peopleitem_right">
                        <div class="atp_peopleitem_inputbox">
                          <el-select v-model="notice" placeholder="请选择" size="small">
                            <el-option v-for="data in noticCopyList" :key="data.id" :label="data.name" :value="data.id">
                            </el-option>
                          </el-select>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="full_line"></div>
              <div class="btn_box">
                <div class="btn-blue" @click="commitApplyProcess">保存</div>
                <!-- <div class="btn-blue-border">流程检查</div> -->
              </div>
            </div>
          </div>
        </div>
        <add-people-modal :visibleDialog="copierVisibleDialog" name="copier" :selectedId="copierIdList" @changeshow="changeModalShow" @setData="getModaldata" title="选择默认抄送人"></add-people-modal>
        <add-people-modal :visibleDialog="operatorVisibleDialog" name="operator" title="选择默认经办人" :selectedId="operatorIdList" @changeshow="changeModalShow" @setData="getModaldata"></add-people-modal>
      </div>
      <apply-temp-right :showright="showright" @changeshow="changerightshow" @setData="setApplyData" :data="applierItem"></apply-temp-right>
    </div>
  </div>
</template>
<script>
//审批流程状态
import { dialogConfirm, compareJson, dialogConfirmHasbtn, setStore, getStore, removeStore } from '../../config/mUtils'
import { showApprovalProcess, getUserCompany, setApprovalProcess, editCondition, deleteCondition } from '../../config/api'
import applyTempRight from './applyTempRight.vue' //右部模块
import addPeopleModal from './addPeopleModal.vue' //右部模块
import draggable from 'vuedraggable'
export default {
  data() {
    return {
      id: '',
      template_id: 2,
      type: '',
      showright: false,
      //用户列表
      allVisibleStr: [],
      hasCopyapplier: false,
      /////////////////////////////更多设置
      copyAplier: [],
      //去重选中
      filter: 0,
      //通知抄送人选中
      notice: 1,

      //开启去重
      clearRepeatList: [{
        id: 0,
        name: '不启用自动去重'
      }, {
        id: 1,
        name: '同一个审批人在流程中出现多次时，仅保留第一个'
      }, {
        id: 2,
        name: '同一个审批人在流程中出现多次时，仅保留最后一个'
      }, {
        id: 3,
        name: '同一个审批人仅在连续出现时，自动去重'
      }],
      //通知抄送人
      noticCopyList: [{
        id: 1,
        name: '仅全部同意后通知'
      }, {
        id: 2,
        name: '仅发起时通知'
      }, {
        id: 3,
        name: '发起时和全部同意后均通知'
      }],
      //右侧弹窗数据
      applierItem: {
        name: '',
        class: 1, //审批类别
        pattern: 1, //审批方式
        uid: [],
        uidList: [],
      },
      //审批人列表
      applierList: [],
      ////////////////////条件列表
      conditionChange: false, //条件列表是否有做修改
      conditionList: [],
      //抄送人弹窗///////////////////////////////
      copierVisibleDialog: false,
      //抄送人列表
      copierList: [],
      copierIdList: [],
      // defaultExpandedArr:[],
      //经办人人弹窗///////////////////////////////
      operatorVisibleDialog: false,
      //经办人列表
      operatorList: [],
      operatorIdList: [],
      // defaultExpandedArr:[],
    }
  },
  created() {
    if (this.$route.query.template_id != undefined && this.$route.query.type != undefined && this.$route.query.template_id != '' && this.$route.query.type != '') {
      this.template_id = this.$route.query.template_id;
      this.type = this.$route.query.type;
      this.getUserRule();
    } else {
      this.$router.push({
        name: 'applytempList'
      })
    }
  },
  components: {
    applyTempRight,
    draggable,
    addPeopleModal
  },
  computed: {},
  mounted() {},

  methods: {
    initData() {
      showApprovalProcess({
        template_id: this.template_id
      }).then(res => {
        if (res.data.msgNo == 1) {
          //条件
          //this.conditionList = [];
          //this.oldConditionList = [];
          this.conditionList = JSON.parse(JSON.stringify(res.data.payload.condition));
          this.filter = res.data.payload.set.filter;
          this.notice = res.data.payload.set.notice;
          //审批人
          this.applierList = res.data.payload.default.sp;
          //抄送人
          this.copierList = res.data.payload.default.copier;
          this.copierIdList = [];
          this.copierList.forEach(item => {
            this.copierIdList.push(item.id);
          })
          //经办人
          this.operatorList = [];
          this.operatorIdList = [];
          if (res.data.payload.default.handle.user == undefined || res.data.payload.default.handle.user.length == 0) {} else {
            this.operatorList = res.data.payload.default.handle.user;
            this.operatorList.forEach(item => {
              this.operatorIdList.push(item.id);
            })
          }

        }
      })
    },
    removeHandle(event) {
      this.conditionChange = true;
    },
    //提交保存
    commitApplyProcess() {
      if (this.applierList.length == 0) {
        dialogConfirm('info', '审批人不能为空');
        return;
      }
      let param = {};
      let splist = [];
      let df = {};
      this.applierList.forEach(item => {
        let sp = {};
        sp.name = item.name;
        sp.uid = item.uid.toString();
        sp.class = item.class;
        sp.pattern = item.pattern;
        splist.push(sp);
      })
      df.sp = splist;
      df.copier = this.copierIdList.toString();
      if (this.type == 2 || this.type == 3) {
        df.handle = this.operatorIdList.toString();
      }
      param.default = df;
      param.filter = this.filter;
      param.notice = this.notice;
      if (this.conditionChange) {
        let conditionList = [];
        this.conditionList.forEach(item => {
          let conditionitem = {};
          let splist = [];
          item.sp.forEach(item1 => {
            let sp = {};
            sp.name = item1.name;
            sp.uid = item1.uid.toString();
            sp.class = item1.class;
            sp.pattern = item1.pattern;
            splist.push(sp);
          })
          conditionitem.type = item.type.toString();
          conditionitem.data_id = item.data_id;
          conditionitem.sp = splist;
          if (this.type == 2 || this.type == 3) {
            conditionitem.handle = item.handle.uid;
          }
          conditionList.push(conditionitem);
        })
        param.condition = conditionList;
      }
      setApprovalProcess({
        template_id: this.template_id,
        data: JSON.stringify(param)
      }).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '修改成功');
          this.initData();
        } else {
          dialogConfirm('fail', res.data.message);
        }
      })
    },
    //获取用户权限信息
    getUserRule() {
      getUserCompany({
        is_all: 1
      }).then(res => {
        if (res.data.msgNo == 1) {
          let vl = res.data.payload;
          let allVisibleStr = [];
          vl.forEach(item => {
            if (item.children != undefined) {
              item.children.forEach(item1 => {
                allVisibleStr.push(item1);
              })
            }
          })
          this.initData();
          this.allVisibleStr = allVisibleStr;
        } else {
          dialogConfirm('fail', res.data.message);
        }
      })
    },
    changeArrayString(string) {
      let array = string.toString().split(',');
      let arrayInt = [];
      array.forEach(item => {
        arrayInt.push(parseInt(item))
      })
      return arrayInt;
    },
    //复制条件审批审批人
    copyAplierFun(index) {
      this.copyAplier = this.conditionList[index].sp;
      dialogConfirm('success', '复制成功');
      this.hasCopyapplier = true;
    },
    pasteAplierFun(index) {
      if (this.hasCopyapplier == false) {
        dialogConfirm('info', '请先复制审批人');
        return;
      }
      let newsp = this.conditionList[index].sp.concat(this.copyAplier);
      if (this.copyAplier.length != 0) {
        let param = {};
        let splist = [];
        newsp.forEach(item => {
          let sp = {};
          sp.name = item.name;
          sp.uid = item.uid.toString();
          sp.class = item.class;
          sp.pattern = item.pattern;
          splist.push(sp);
        })
        param.sp = splist;
        param.type = this.conditionList[index].type.toString();
        param.data_id = this.conditionList[index].data_id;
        param.handle = this.conditionList[index].handle.uid;
        editCondition({
          id: this.conditionList[index].id,
          data: JSON.stringify(param)
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '粘贴成功');
            this.conditionList[index].sp = newsp;
          } else {
            dialogConfirm('fail', res.data.message);
          }
        })
      } else {
        dialogConfirm('success', '粘贴成功')
      }
    },
    //删除条件审批
    deleteConditionFun(id, index) {
      dialogConfirmHasbtn('确认删除吗？').then(res => {
        deleteCondition({
          id: id
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '删除成功')
            this.conditionList.splice(index, 1)
          }
        })
      }).catch(() => {})
    },
    //新增条件审批
    addCondition() {
      this.$router.push({
        name: 'applytempProcessCondition',
        query: {
          edittype: 1, //=>新增，2=>编辑
          type: this.type,
          template_id: this.template_id
        }
      })
    },
    editCondition(index) {
      setStore('htcx_applyCondition', this.conditionList[index]);
      this.$router.push({
        name: 'applytempProcessCondition',
        query: {
          edittype: 2, //=>新增，2=>编辑
          type: this.type,
          template_id: this.template_id
        }
      })
    },
    //新增审批人
    addApplyPeople() {
      this.showright = true;
      this.applierItem = {
        rightType: 1,
        name: '',
        class: 1, //审批类别
        pattern: 1, //审批方式
        uid: [],
        user: [],
      };
    },
    //修改审批人
    editApplyPeople(index, item) {
      this.applierItem = item;
      this.applierItem.rightType = 2;
      this.applierItem.rightIndex = index;
      this.showright = true;
    },
    //删除审批人
    deleteApplyPeople(index) {
      this.applierList.splice(index, 1)
    },
    //右侧数据传递
    changerightshow(type) {
      this.showright = type;
    },
    //新增默认审批人
    setApplyData(data) {
      let newdata = {};
      newdata.class = data.class;
      newdata.name = data.name;
      newdata.pattern = data.pattern;
      newdata.uid = data.uid;
      newdata.user = data.user;
      if (data.rightType == 1) {
        this.applierList.push(newdata);
      } else {
        this.applierList[data.rightIndex] = newdata;
      }
      this.showright = false;
    },

    //新增/编辑抄送人
    addCopyPeople() {
      this.copierVisibleDialog = true;
    },
    //新增/修改经办人
    addOperatorPeople() {
      this.operatorVisibleDialog = true;
    },
    //删除抄送人/经办人
    deleteUser(name, id, index) {
      let name1 = name + 'List';
      let name2 = name + 'IdList';
      this[name1].splice(index, 1);
      this[name2] = this[name2].filter(item => {
        return item != id
      });
    },
    ///////////////////////////弹窗数据传递
    //修改弹窗显示
    changeModalShow(type, name) {
      this[name + 'VisibleDialog'] = type
    },
    getModaldata(name, list) {
      this[name] = list;
    },
    getImgurl(e) {
      e.currentTarget.src = '../../static/images/header_userimg.png';
      e.onerror = null;
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';

</style>



