<template>
  <div>
    <transition name="foldbox">
      <div class="table_right" v-show="showright">
        <div class="table_right_cover" @click="changeshow(false)"></div>
        <transition name="fold">
          <div class="table_right_body" v-show="showright">
            <div class="table_right_title">
              设置审批人
            </div>
            <div class="full_line marginb_30"></div>
            <div class="table_right_content">
              <div class="atp_peoplelist">
                <div class="atp_peopleitem">
                  <div class="atp_peopleitem_left">审批名称</div>
                  <div class="atp_peopleitem_right">
                    <div class="atp_peopleitem_inputbox marginb_20">
                      <el-input v-model="currentApplierItem.name" placeholder="请输入内容" size="small"></el-input>
                    </div>
                  </div>
                </div>
                <div class="full_line marginb_20"></div>
                <div class="atp_peopleitem marginb_30">
                  <div class="atp_peopleitem_left">审批人类别</div>
                  <div class="atp_peopleitem_right">
                    <div class="marginb_20">
                      <el-radio-group v-model="currentApplierItem.class" class="atp_radiogroup flex">
                        <el-radio v-for="(item,index) in applierTypeList" :key="index" :label="item.id" class="atp_radioitem">{{item.name}}</el-radio>
                      </el-radio-group>
                    </div>
                    <div class="full_line marginb_20" v-if="currentApplierItem.class!=1"></div>
                    <div v-if="currentApplierItem.class==2" class="flex">
                      <div class="atp_peopleitem_addeditem marginr_10" v-if="userList.length!=0">
                        <span class="iconfont icon-guanbi" @click="clearApplier"></span>
                        <div class="atp_peopleitem_addeditem_top">
                          <div class="atp_peopleitem_peoplebox" v-for="item in userList">
                            <img :src="item.url" @error="getImgurl" />
                            <span>{{item.name}}</span>
                          </div>
                        </div>
                      </div>
                      <div class="atp_btnbox flex_shrink">
                        <div class="btn-blue" v-if="userList.length==0" @click="addCopyPeople">添加成员</div>
                        <div class="btn-blue" v-else @click="addCopyPeople">修改成员</div>
                      </div>
                    </div>
                    <div class="atp_peopleitem_inputbox" v-if="currentApplierItem.class==3">
                      <el-select v-model="roleid" placeholder="请选择角色" size="small">
                        <el-option v-for="(data,index) in roleList" :key="index" :label="data.name" :value="data.id">
                        </el-option>
                      </el-select>
                    </div>
                  </div>
                </div>
                <div class="full_line marginb_20"></div>
                <div class="atp_peopleitem marginb_30">
                  <div class="atp_peopleitem_left">审批方式
                    <el-tooltip class="atp_group_title_tip" effect="dark" content="当审批人含有多人时的审批方式" placement="top">
                      <i class="el-icon-question"></i>
                    </el-tooltip>
                  </div>
                  <div class="atp_peopleitem_right">
                    <div>
                      <el-radio-group v-model="currentApplierItem.pattern" class="atp_radiogroup_column flex_column">
                        <el-radio v-for="(item,index) in applyTypeList" :key="index" :label="item.id" class="atp_radioitem">{{item.name}}</el-radio>
                      </el-radio-group>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="table_right_bottom flex">
              <div class="btn-blue" @click="commitData">确定</div>
              <div class="btn-blue-border" @click="changeshow(false)">取消</div>
            </div>
          </div>
        </transition>
      </div>
    </transition>
    <add-people-modal :visibleDialog="userVisibleDialog" name="user" :selectedId="userIdList" @changeshow="changeModalShow" @setData="getModaldata" title="选择指定成员"></add-people-modal>
  </div>
</template>
<script>
import addPeopleModal from './addPeopleModal.vue' //右部模块
import { dialogConfirm, compareJson, dialogConfirmHasbtn } from '../../config/mUtils'
import { getCharacter } from '../../config/api'
export default {
  name: 'draglist',
  data() {
    return {
      radio2: '',
      currentApplierItem: {
        name: '',
        class: 1, //审批类别
        pattern: 1, //审批方式
        uid: [],
        uidList: [],
      },
      roleid: '',
      //指定成员弹窗
      userList: [],
      userIdList: [],
      userVisibleDialog: false,
      applierTypeId: 1,
      applierTypeList: [{
        id: 1,
        name: '主管'
      }, {
        id: 2,
        name: '指定成员'
      }, {
        id: 3,
        name: '角色'
      }],
      roleList: [{
        id: 1,
        name: '主管'
      }, {
        id: 2,
        name: '指定成员'
      }, {
        id: 3,
        name: '角色'
      }],
      applyTypeId: 1,
      applyTypeList: [{
        id: 1,
        name: '依次审批（本环节内审批人依次审批）'
      }, {
        id: 2,
        name: '会签（须所有审批人同意）'
      }, {
        id: 3,
        name: '或签（一名审批人同意或拒绝即可）'
      }]
    }
  },

  props: ['showright', 'data'],
  components: {
    addPeopleModal
  },
  watch: {
    showright() {
      this.currentApplierItem = JSON.parse(JSON.stringify(this.data));
      if (this.data.class == 2) {
        this.userIdList = this.data.uid.toString().split(',');
        this.userList = this.data.user;
      } else {
        this.userIdList = [];
        this.userList = [];
      }
      if(this.data.class==3){
        this.roleid=this.data.uid
      }else{
        this.roleid='';
      }
      this.initData();
    }
  },
  methods: {
    initData(){
      getCharacter().then(res=>{
        if(res.data.msgNo==1){
          this.roleList=res.data.payload;
          if(this.roleList.length==0){
            this.roleid='';
          }else if(this.roleid==''){
            this.roleid=this.roleList[0].id
          }
        }
      })
    },
    changeshow(type) {
      this.$emit('changeshow', type);
    },
    commitData() {
      let param = JSON.parse(JSON.stringify(this.currentApplierItem));
      if (param.class == 1) {
        param.user = [];
        param.uid = [];
      } else if (param.class == 2) {
        if (this.userIdList.length == 0) {
          dialogConfirm('info', '指定成员不能为空～');
          return;
        } else {
          param.uid = this.userIdList;
          param.user = this.userList;
        }
      } else {
        if (this.roleid != '' && this.roleid != null) {
          param.uid = this.roleid;
          param.user = this.roleList.filter(item => {
            return item.id == this.roleid;
          })
        } else {
          dialogConfirm('info', '角色不能为空～')
          return;
        }
      }
      this.$emit('setData', param);
      this.changeshow(false);
    },
    clearApplier() {
      this.userIdList = [];
      this.userList = [];
    },
    ///////////////////////////弹窗数据传递
    //修改弹窗显示
    addCopyPeople() {
      this.userVisibleDialog = true;
    },
    changeModalShow(type, name) {
      this[name + 'VisibleDialog'] = type
    },
    getModaldata(name, list) {
      this[name] = list;
    },
    getImgurl(e) {
      e.currentTarget.src = '../../static/images/header_userimg.png';
      e.onerror = null;
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';

</style>



